import React, { Component } from "react";

import Article from "grommet/components/Article";
import Section from "grommet/components/Section";
import Headline from "grommet/components/Headline";
import Paragraph from "grommet/components/Paragraph";

export default class Articles extends Component {
  render() {
    return (
      <Article scrollStep={true} controls={false}>
        <Section pad="large" justify="center" align="center" full="vertical">
          <Headline margin="none">Section 1</Headline>
          <Paragraph size="large">
            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Illum
            unde, quae in provident reiciendis at, modi amet temporibus nulla,
            dolore molestias quisquam. Itaque aliquid libero voluptatem
            consequuntur fugiat hic dolore.
          </Paragraph>
          <p>
            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Illum
            unde, quae in provident reiciendis at, modi amet temporibus nulla,
            dolore molestias quisquam. Itaque aliquid libero voluptatem
            consequuntur fugiat hic dolore.
          </p>
        </Section>
        <Section
          pad="large"
          justify="center"
          align="center"
          full="vertical"
          colorIndex="accent-1"
        >
          <Headline margin="none">Section 2</Headline>
        </Section>
        <Section pad="large" justify="center" align="center" full="vertical">
          <Headline margin="none">Section 3</Headline>
        </Section>
        <Section
          pad="large"
          justify="center"
          align="center"
          full="vertical"
          colorIndex="accent-1"
        >
          <Headline margin="none">Section 4</Headline>
        </Section>
        <Section pad="large" justify="center" align="center" full="vertical">
          <Headline margin="none">Section 5</Headline>
        </Section>
      </Article>
    );
  }
}



